.home-page {
  background-image: url(../images/SVG/pixabay.svg);
  background-repeat: no-repeat;
  background-size: cover;
}

.explore {
  margin-top: 3rem;
}

.chevrons-down {
  display: flex;
  flex-direction: column;
}
.chevrons-down a {
  position: relative;
}
.chevrons-down span {
  position: absolute;
  left: auto;
  right: auto;
}
.chevrons-down .chev-1 {
  -webkit-animation: move 1s infinite linear;
          animation: move 1s infinite linear;
  bottom: -16px;
}
.chevrons-down .chev-2 {
  -webkit-animation: move 1s infinite linear;
          animation: move 1s infinite linear;
  bottom: -35px;
}

@-webkit-keyframes move {
  0% {
    margin-bottom: 0;
  }
  50% {
    margin-bottom: 5px;
  }
  100% {
    margin-bottom: 0;
  }
}

@keyframes move {
  0% {
    margin-bottom: 0;
  }
  50% {
    margin-bottom: 5px;
  }
  100% {
    margin-bottom: 0;
  }
}/*# sourceMappingURL=homepage.css.map */